<template>
<div class=" p-3">
    <div class="display-2 font-weight-light mt-2"> Gestion des agences </div>
    <el-divider class="mt-2"></el-divider>
    <el-skeleton :rows="20" animated v-if="loading" />
    <div v-else>
        <el-table :data="all_data.filter(data => !search || data.code.toLowerCase().includes(search.toLowerCase()))" style="width: 100%">
            <el-table-column label="Code" prop="code"> </el-table-column>
            <el-table-column label="Nom" prop="nom"> </el-table-column>
            <el-table-column label="Ville" prop="ville"> </el-table-column>
            <el-table-column label="Adresse" prop="adresse"> </el-table-column>
            <el-table-column align="right">
            <template slot="header" slot-scope="scope">
                <el-input  v-model="search"  size="mini" placeholder="Type to search"/>
            </template>
            <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">Editer</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">Supprimer</el-button>
            </template>
            </el-table-column>
        </el-table>

        <v-dialog v-model="dialog" max-width="600" max-heigth="500">
            <ModalAgenceComponent v-bind:dialog="dialog" />
        </v-dialog>
    </div>
</div>
</template>

<script>

    import NProgress from 'nprogress';
    import 'nprogress/nprogress.css';
    import axios from 'axios';
    import moment from 'moment';
    import ModalAgenceComponent from '@/components/dashboard/Settings/Agence/ModalAgenceComponent.vue';



    export default {
        
        components: {
            ModalAgenceComponent
        },
        data() {
            return {
                all_data: [{
                date: '2016-05-03',
                name: 'Tom',
                address: 'No. 189, Grove St, Los Angeles'
                }, {
                date: '2016-05-02',
                name: 'John',
                address: 'No. 189, Grove St, Los Angeles'
                }, {
                date: '2016-05-04',
                name: 'Morgan',
                address: 'No. 189, Grove St, Los Angeles'
                }, {
                date: '2016-05-01',
                name: 'Jessy',
                address: 'No. 189, Grove St, Los Angeles'
                }],
                search: '',
                loading: true,
                dialog: false,
                form: '',
            }
        },

        created(){
            this.loadData();
        },
        methods: {

        async loadData()
        {
            NProgress.start();

            try {
              
                const response = await axios.get('v1/xperf/setting/agence/get');

                this.all_data = response.data;
                this.loading = false;


            } 
            catch (error) {

                this.loading = false;
                this.$notify({
                    type: 'error',
                    title: "Echec de recuperation"
                });
            }

            NProgress.done();

        },

            handleEdit(index, row) {
                this.dialog = 1;
                this.form = row;
            },

            handleDelete(index, row) {
                console.log(index, row);
            }
        },
    }
</script>

<style>
    
    element.style {
        overflow:hiden;
    }
</style>